<template>
  <div :id="`geoMap-${id}`">
    <svg width="500" height="500" style="border: 1px solid #00000060">
      <g class="counties"></g>
      <path class="county-borders"></path>
    </svg>
  </div>
</template>
<script>
import axios from "axios";
import * as d3 from "d3";
import shortid from "shortid";
import * as topojson from "topojson-client";
export default {
  name: "DrawMap",
  data() {
    return {
      id: shortid.generate(),
      DataAPI: "/COUNTY_MOI_1081121topo.json",
      MapData: undefined,
      population:
        "/1-2+土地面積、戶數、人口數、人口密度、年齡結構及扶養比例按主要都市分.csv"
    };
  },
  mounted() {
    const vm = this;
    axios
      .get(this.DataAPI)
      .then(res => {
        // eslint-disable-next-line no-console
        console.log("取得地圖JSON檔", res);
        vm.MapData = res.data;
        this.draw();
      })
      .catch(err => {
        // eslint-disable-next-line no-console
        console.log("err", err);
      });
  },
  methods: {
    getData(api) {
      const vm = this;
      axios
        .get(api)
        .then(res => {
          // eslint-disable-next-line no-console
          console.log("取得地圖JSON檔", res);
          vm.MapData = res;
        })
        .catch(err => {
          // eslint-disable-next-line no-console
          console.log("err", err);
        });
    },
    draw() {
      const vm = this;
      const geoMap = d3
        .geoMercator()
        .center([123, 23.5])
        .scale(6000);

      // eslint-disable-next-line no-console
      console.log(
        topojson.feature(vm.MapData, vm.MapData.objects["COUNTY_MOI_1081121"])
      );
      const counties = topojson
        .feature(vm.MapData, vm.MapData.objects["COUNTY_MOI_1081121"])
        .features.map(item => {
          return item.properties.COUNTYNAME;
        });
      // eslint-disable-next-line no-console
      console.log("counties", counties);
      const population = d3.csv(vm.population, d => {
        return {
          county: d["地區"],
          population: d["年底人口數"],
          density: d["人口密度"]
        };
      });
      // eslint-disable-next-line no-console
      console.log("population", population);
      const geoPath = d3.geoPath(geoMap);
      d3.select("g.counties")
        .selectAll("path")
        .data(
          topojson.feature(vm.MapData, vm.MapData.objects["COUNTY_MOI_1081121"])
            .features
        )
        .enter()
        .append("path")
        .attr("d", geoPath);
      d3.select("path.county-borders").attr(
        "d",
        geoPath(
          topojson.mesh(vm.MapData, vm.MapData.objects["COUNTY_MOI_1081121"])
        )
      );
    }
  }
};
</script>
<style lang="scss">
.counties {
  fill: #547480;
}
.counties :hover {
  fill: #6c858e;
  transition: 0.1s;
}
.county-borders {
  fill: none;
  stroke: #fff;
  stroke-width: 0.5px;
}
</style>
